.fixed-top {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    background-color: #f0f0f0;
    padding: 20px;
}

.triangleTrust {
    width: 100%;
    height: 100%;
    border-left: 12px solid transparent;
    border-right: 12px solid transparent;
    border-top: 12px solid black; /* Color and size can be adjusted as needed */
    position: relative;
    top: 0px; /* Adjust the value here so that the subscript is positioned appropriately */
    display: inline-block;
    vertical-align: middle;
}
.triangleWai {
    position: absolute;
    left: -20px; /* 调整左移的距离，可以根据需要进行调整 */
    top: 50%; /* 将三角形垂直居中 */
    transform: translateY(-50%); /* 使三角形垂直居中 */
    width: 0;
    height: 0;
    border-style: solid;
    border-width: 20px 0 20px 20px; /* 设置三角形的大小和形状 */
    border-color: transparent transparent transparent black; /* 设置三角形的颜色 */
}
.content-wrapper {
    margin-top: 20px; /* Make sure the content below is not obscured by the fixed div */
    display: flex;
}

.fixed-left {
    flex: 22%; /* Fixed width of div on the left  */
    margin-top: 1em;
    margin-left: 50px;
    margin-right: 50px;
    background-color: #d0d0d0;
    box-sizing: border-box;
    padding: 20px;
}
.right-container{
    flex: 78%; /* Fixed width of div on the right */
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
}
.scrollable-right {
    margin-top: 1em;
    flex-grow: 1; /* The scrollable div on the right occupies the remaining width */
    /*background-color: #d0d0d0;*/
    margin-right: 50px;
    padding: 20px;
    box-sizing: border-box;
    overflow-y: auto; /* Allow vertical scrolling */
}
.fixed-below-scrollable {
    margin-top: 1em;
    margin-right: 50px;
    background-color: #e0e0e0;
    height: 30px;
    box-sizing: border-box;
}
.fixed-below-Pou-scrollable {
    position:relative;
    z-index:1;
    margin-right: 50px;
    margin-top: 2em;
    margin-bottom: 1px;
    height: 30px;
    box-sizing: border-box;
}
